'use client';

import React, { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { authService } from '@/services/api/auth.service';
import { cookies } from '@/utils/cookies';
import toast from 'react-hot-toast';

export default function LoginByTokenPage() {
  const { token } = useParams();
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const handleLoginByToken = async () => {
      if (!token) {
        setError('Token não fornecido');
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        // Fazer login usando o token
        const response = await authService.loginByToken(token as string);
        
        if (response.message && response.data) {
          const { user, token: jwtToken } = response.data;
          
          toast.success('Login realizado com sucesso!');
          
          // Salvar token e dados do usuário em cookies
          cookies.token.set(jwtToken);
          cookies.user.set(JSON.stringify(user));
          
          // Pequeno delay para garantir que o usuário veja a mensagem de sucesso
          await new Promise(resolve => setTimeout(resolve, 1000));
          
          // Redirecionar baseado no role do usuário
          const defaultPath = {
            SUPERADMIN: "/dashboard/admin",
            ADMIN: "/dashboard/empresa",
            GESTOR: "/dashboard/empresa",
            VENDEDOR: "/dashboard/empresa",
            CLIENTE: "/dashboard/cliente"
          }[user.role] || "/";
          
          // Aguardar o toast e depois redirecionar
          setTimeout(() => {
            if (typeof window !== 'undefined') {
              // window.location.href = defaultPath;
              router.push(defaultPath);
              setTimeout(() => {
                window.location.reload();
              }, 1500);
            }
          }, 1500);
        } else {
          throw new Error('Resposta inválida do servidor');
        }
      } catch (error: any) {
        console.error('Erro no login por token:', error);
        
        const errorMessage = error.response?.data?.error || 
                           error.message || 
                           'Erro ao fazer login com o token';
        
        setError(errorMessage);
        toast.error(errorMessage);
        
        // Redirecionar para página de login após 3 segundos
        setTimeout(() => {
          router.push('/auth/login');
        }, 3000);
      } finally {
        setLoading(false);
      }
    };

    handleLoginByToken();
  }, [token, router]);

  if (loading) {
    return (
      <div className="flex flex-col flex-1 lg:w-1/2 w-full">
        <div className="w-full max-w-md sm:pt-10 mx-auto mb-5">
          <div className="flex flex-col justify-center flex-1 w-full max-w-md mx-auto">
            <div className="mb-5 sm:mb-8">
              <h1 className="mb-2 font-semibold text-gray-800 text-title-sm dark:text-white/90 sm:text-title-md">
                Fazendo login...
              </h1>
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Aguarde enquanto verificamos seu token de acesso.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="max-w-md w-full space-y-8">
          <div className="text-center">
            <div className="mx-auto h-12 w-12 text-red-500">
              <svg fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
              </svg>
            </div>
            <h2 className="mt-6 text-3xl font-extrabold text-gray-900 dark:text-white">
              Erro no Login
            </h2>
            <p className="mt-2 text-sm text-gray-600 dark:text-gray-400">
              {error}
            </p>
            <div className="mt-6">
              <button
                onClick={() => router.push('/auth/login')}
                className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
              >
                Ir para Login
              </button>
            </div>
            <p className="mt-4 text-xs text-gray-500 dark:text-gray-400">
              Você será redirecionado automaticamente em alguns segundos...
            </p>
          </div>
        </div>
      </div>
    );
  }

  return null;
}
